import React, { useState, useEffect } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import axios from "axios";
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import { addToCart } from "../../redux/amazonSlice";

const Lastestpr = () => {
  const settings = {
    className: "firstline",
    dots: true,
    infinite: true,
    slidesToShow: 4,
    swipeToslide: true,
    afterChange: function (index) {
      console.log(
        `Slider Changed to: ${index + 1}, background: #222; color: #bada55`
      );
    },
  };
  const dispatch = useDispatch();
  const [data, setData] = useState([]);
  useEffect(() => {
    axios
      .get(
        "https://digitalinstitute-amazon.azurewebsites.net/api/product/latestproducts"
      )
      .then((result) => setData(result.data));
  }, []);

  return (
    <div className=" mt-2 mr-5 ml-5 mb-6">
      <div>
        <h1 className=" text-xl font-bold ">Latest Products</h1>
        <div>
          <Slider {...settings}>
            {data.map((item) => (
              <div
                key={item.id}
                className=" m-auto max-w-[224px] max-h-[284px] cover object-center "
              >
                <Link key={item.id} to="/productdetails">
                <img  className=" m-auto max-w-[224px] max-h-[184px] cover object-center" src={`${item.image}`}></img>
                <p className="font-bold text-lg">
                  <span className=" text-lg font-bold  ">$</span>
                  {item.price}
                </p>
                <p className="line-clamp-1 text-xs">{item.name}</p>
                </Link>
              
                <Link key={item.id} to="/cart">
                  <button
                    onClick={() =>
                      dispatch(
                        addToCart({
                          id: item.id,
                          title: item.title,
                          description: item.description,
                          price: item.price,
                          category: item.category,
                          image: item.image,
                          quantity: 1,
                        })
                      )
                    }
                    className="w-full py-0.5 rounded-md mt-2 font-titleFont font-medium text-base bg-gradient-to-tr from-yellow-400 to-yellow-100 border border-yellow-500 hover:border-yellow-700 hover:from-yellow-300 to hover:to-yellow-400 active:bg-gradient-to-bl active:from-yellow-400 active:to-yellow-500 duration-200"
                  >
                    Add to Cart
                  </button>
                </Link>
              </div>
            ))}
          </Slider>
        </div>
      </div>
    </div>
  );
};

export default Lastestpr;
